.address {
  &__label {
    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;

    color: #9fadc2;
    margin-bottom: 8px;
  }

  &__content {
    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;

    color: #3e3e3e;
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 120px;
    }
  }
}
